<template>
	<div >
	<canvas  class="canvas"></canvas>
    <s-sticky>
		<v-header class="position-top"></v-header>
    </s-sticky>
   <!-- <sidebar></sidebar>-->
    <div class="vbody">
      <el-card class="box-card" >
        <transition>
          <router-view></router-view>
        </transition>
      </el-card>
		</div>
    <v-footer class="secondary" app>
      <v-layout row wrap align-center>

        <v-flex xs12>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn icon class="deep-purple--text">
              <v-icon medium>fa-weixin</v-icon>
            </v-btn>
            <v-btn icon class="light-blue--text">
              <v-icon medium>fa-qq</v-icon>
            </v-btn>
            <v-btn icon class="blue--text text--darken-4">
              <v-icon medium>fa-phone</v-icon>
            </v-btn>
          </v-card-actions>
        </v-flex>
      </v-layout>
    </v-footer>
    <BackTop :height="100" :bottom="40">
      <div class="top">top</div>
    </BackTop>

	</div>
</template>
<script>

import Header from 'components/layout/Header'
import Sidebar from 'components/layout/Sidebar'
import BackToTop from '@/components/BackToTop'
import Sticky from '@/components/Sticky'
export default {
	data() {
    return {
      myBackToTopStyle: {
        right: '50px',
        bottom: '50px',
        width: '40px',
        height: '40px',
        'border-radius': '4px',
        'line-height': '45px', // 请保持与高度一致以垂直居中 Please keep consistent with height to center vertically
        background: '#e7eaf1'// 按钮的背景颜色 The background color of the button
      }
    }
	},
	mounted() {
	},
	components: {
		"v-header": Header,
		Sidebar,
    "back-to-top":BackToTop,
    "s-sticky":Sticky,
	}
}
</script>

<style>
  .box-card {
    max-width: 100%;
  }
.vbody{
  padding:3px 5px 5px 5px;
  height:100%;
	color: #fff;
	box-sizing: border-box;
	overflow-x: hidden;
	background: #ebeef5;
  z-index: -1;
}
.canvas {
    position: fixed;
    top:0;
    left:0;
    z-index: 1;
}
.position-top{
  position: absolute;
  z-index: 2;

}
#home a{
	color: #fff;
	display: block;
}
.content{
	 height: 100%;
	 margin-top: -50px;
	display: flex;
}

.content section{
	flex: 1;
	z-index: 11;
	margin-left: 200px;
  box-sizing: border-box;
	padding-right: 60px;
}

table{
	position: absolute;
	top: 100px;
	padding: 0 20px;
	width: 100%;
}
table thead tr{
	border-bottom: 2px solid #fff;
}
table tbody tr{
	border-bottom: 1px solid #fff;
}
table tr{
	height: 50px;
}
table tr td{
	text-align: center;
	cursor: pointer;
}
table tr td:last-child{
	width: 10%;
}
table tr th{
	font-weight: normal;
	cursor: pointer;
}

.search-bar{
	position: absolute;
	top: 20px;
	left: 10%;
	width: 400px;
	height: 46px;
	line-height: 46px;
}
.search-bar .input-wrap{
	float: left;
	width: 270px;
	height: 40px;
	padding: 0 15px;
	line-height: 40px;
	border: none;
	outline: none;
	font-size: 20px;
	border-radius: 20px 0 0 20px;
	background: rgba(255, 255, 255, 0.2);
	transition: all .8s;
}
.search-bar .input-wrap:focus{
	background: rgba(255, 255, 255, .8);
}
.search-bar .input-button{
	float: left;
	width: 50px;
	height: 38px;
	line-height: 38px;
	text-align: center;
	font-size: 14px;
	border: 1px solid rgba(255, 255, 255, 0.2);
	transition: all .8s;
}
.search-bar .input-wrap:focus .input-botton{
	border-color: rgba(255, 255, 255, 0.8);
}
.fade-enter-active {
  	transition: all .5s
}
.fade-enter, .fade-leave-active {
  	opacity: 0;
  	transform: rotate( -70deg ) translateX( 300px );
}
.top{
  padding: 12px;
  background: rgba(0, 153, 229, .7);
  color: #fff;
  text-align: center;
  border-radius: 20px;
}
</style>



