<template>
  <el-menu
    :default-active="activeIndex2"
    mode="horizontal"
    @select="handleSelect"
    background-color="#41519f"
    text-color="#fff"
    active-text-color="#ffd04b"
    >
    <el-menu-item class="aleftelement" index="0"><a class="loggv" href="javascript:;" ><img src="../../assets/images/eleme.png"/></a></el-menu-item >
    <el-menu-item index="1">退出</el-menu-item>
    <el-menu-item index="2">办公工具</el-menu-item>
    <el-menu-item index="3">每日头条</el-menu-item >
    <el-menu-item index="4">博客</el-menu-item >
  </el-menu>
</template>
<script>
  export default {
    data() {
      return {
        activeIndex: '1',
        activeIndex2: '1'
      };
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key);
        switch (Number(key)) {
          case 1:
            this.$router.push('/customer-login')
            break
          case 3:
            this.$router.push('/headlines')
            break
          case 4:
            this.$router.push('/homePage')
            break
          default:
            this.$router.push('/homePage')
        }
        return
      }
    }
  }
</script>
<style scoped>
  .el-menu--horizontal>li.el-menu-item {
    float: right;
    height: 60px;
    line-height: 60px;
    margin: 0;
    border-bottom: 2px solid transparent;
    color: #909399;
  }
  .el-menu--horizontal>li.el-menu-item.aleftelement {
    float: left;
    height: 60px;
    line-height: 60px;
    margin: 0;
    border-bottom: 2px solid transparent;
    color: #909399;
  }
</style>




