import Vue from 'vue'
import Router from 'vue-router'
import Menu from '@/components/Menu'
import Home from '@/components/Home'


Vue.use(Router)

export default new Router({
	linkActiveClass: 'active',
	routes: [
		{
			path: '/',
			component: Menu
		},
	   	{
	   		path: '/menu',
	   		component: Menu,
	   		children : [{
	   			path : '/customer-login',
	    		// 懒加载
	    		component : resolve => require(['@/components/Login.vue'],resolve)
	    	},{
	    		path : '/admin-login',
	    		// 懒加载
	    		component : resolve => require(['@/components/Login.vue'],resolve)
	    	},{
	    		path : '/regist',
	    		// 懒加载
	    		component : resolve => require(['@/components/Regist.vue'],resolve)
	    	}]
	    },
	    {
	    	path: '/home',
	    	component: Home,
	    	children : [
          {
            path : '/homePage',
            // 懒加载
            component : resolve => require(['components/blog/homePage.vue'],resolve)
          },{
		        path : '/matters',
		        // 懒加载
		        component : resolve => require(['components/Matters.vue'],resolve)
		    },{
            path : '/headlines',
            // 懒加载
            component : resolve => require(['components/headlines/Headlines.vue'],resolve)
          },{
            path : '/writeArticle',
            // 懒加载
            component : resolve => require(['components/article/writeArticle.vue'],resolve)
          }
	    	]
		}
	]
})



